<template>
	<view class="tabs_box">
		<view class="subsection">
			<u-subsection :list="secNav" :current="curNow" active-color="#03958C" mode="subsection" @change="twoChange"></u-subsection>
		</view>
		<!-- 来访转认购周期 -->
		<view class="tabs_box" style="margin-top: -20rpx;" v-if="curNow==0">
			<barDropDown :chartData="chartData" :unitText="'天'" :timeUnit="timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList" :tdList="tdList" :status="6"></utable>
			</view>
		</view>
		<!-- 来访转认购 -->
		<view class="tabs_box" style="margin-top: -20rpx;" v-if="curNow==1">
			<barDropDown :chartData="chartData" :unitText="'%'" :timeUnit="timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList2" :tdList="tdList" :status="6" :type="'rate'"></utable>
			</view>
		</view>
		<!-- 来访转签约 -->
		<view class="tabs_box" style="margin-top: -20rpx;" v-if="curNow==2">
			<barDropDown :chartData="chartData" :unitText="'%'" :timeUnit="timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<view class="express_box">
				<utable :thList="thList2" :tdList="tdList" :status="6" :type="'rate'"></utable>
			</view>
		</view>
	</view>
</template>

<script>
	import barDropDown from '@/components/barDropDown.vue';
	import utable from '@/components/utable.vue';
	export default {
		props:['chartData', 'timeUnit', 'timeSelect', 'tdList'],
		data() {
			return {
				curNow: 0, // 二级导航
				secNav: ['来访转认购周期', '来访转认购','来访转签约'],
				thList:[{name:'序号'},{name:'姓名'},{name:'转化周期'},{name:'与均值相比'}],
				thList2:[{name:'序号'},{name:'姓名'},{name:'转化率'},{name:'与均值相比'}],
				// tdList:[{id:'1',name:'张三',cycle:'1周',val:'200'},{id:'2',name:'李四',cycle:'2周',val:'300'}],
				// 柱状图
				// chartData: {
				// 	categories: ["2012", "2013", "2014", "2015", "2016", "2017", '2018'],
				// 	series: [{
				// 		data: [15, 20, 45, 37, 43, 34, 60]
				// 	}]
				// }
			}
		},
		methods:{
			// 二级tab点击
			twoChange(index) {
				this.curNow = index;
				this.$emit('twoChange', this.curNow)
			},
			// 柱状图左翻
			_prevTap() {
				this.$emit('prevTap')
			},
			// 柱状图右翻
			_nextTap() {
				this.$emit('nextTap')
			},
			// 柱状图单位选择
			_unitTap(index) {
				this.$emit('unitTap',index)
			}
		},
		components:{
			barDropDown, utable
		}
	}
</script>

<style>
</style>
